import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import clsx from 'clsx';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Collapse from '@material-ui/core/Collapse';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import { red } from '@material-ui/core/colors';
import FavoriteIcon from '@material-ui/icons/Favorite';
import ShareIcon from '@material-ui/icons/Share';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import { FaCartPlus } from "react-icons/fa";
import '../Card/card.css'
import SimpleRating from '../Component/Rating/rating'

const useStyles = makeStyles(theme => ({
  card: {
    maxWidth: 235,
    // border:"2px solid",
    display: "inline-block",
    margin: "5px",
    height: "450px",
    marginLeft: "25px"
  },
  media: {
    height: 0,
    paddingTop: '56.25%', // 16:9
  },
  expand: {
    transform: 'rotate(0deg)',
    marginLeft: 'auto',
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    transform: 'rotate(180deg)',
  },
  avatar: {
    backgroundColor: red[500],
  },

  // card:{

  // }
}));

export default function RecipeReviewCard(props) {
  let count = 0;
  console.log(props);
  let value = props;

  const classes = useStyles();
  const [expanded, setExpanded] = React.useState(false);

  function handleExpandClick() {
    setExpanded(!expanded);
  }
  // function onClick(){
  //   let data1={
  //     image:props.img,
  //     name:props.name,
  //     about:props.about
  //   }
  //   console.log(props.onClick(data1))
  // }
  let obj = {
    name: props.name,
    img: props.img,
    about: props.about,
  }
  // console.log(props.onclick())
  // onClick={()=>props.onClick()
  return (
    <Card className={classes.card} onClick={() => props.func(obj)}>

      <CardHeader
        // avatar={
        // <Avatar aria-label="recipe" className={classes.avatar}>
        //   R
        // </Avatar>
        // }
        action={
          <IconButton aria-label="settings">
            {/* <MoreVertIcon /> */}
          </IconButton>
        }
        title={props.name}
        // subheader="September 14, 2016"
        style={{ fontSize: "15px", textAlign: "center" }} />

      <img src={props.img} height="200px" width="200px" style={{ marginLeft: "20px" }} />
      {/* <CardContent> */}
      <Typography variant="body2" color="textSecondary" component="p" style={{ textAlign: "center", fontSize: "20px" }}>

        {props.about}
        <center style={{ marginLeft: "70px" }}><SimpleRating /></center>

        {/* This impressive paella is a perfect party dish and a fun meal to cook together with your
          guests. Add 1 cup of frozen peas along with the mussels, if you like. */}
      </Typography>
      {/* </CardContent> */}
      {/* <b>sdlfdslfjdskljfldsjfkls</b> */}

      <CardActions disableSpacing>

        {/* <button> */}
        <IconButton ariza-label="add to favorites">
          <FavoriteIcon />
        </IconButton>

        <span style={{ fontSize: "10px", color: "blue" }}>Add To WishList</span>

        {/* </button>  */}


        {/* <button> */}
        <IconButton aria-label="share">
          <FaCartPlus />
        </IconButton>
        {/* <button onClick={()=>props.click({value,count})}> */}
        <span style={{ fontSize: "10px", color: "blue" }}>Add To Cart</span>
        {/* </button> */}
        {/* </button> */}
        <IconButton
          className={clsx(classes.expand, {
            [classes.expandOpen]: expanded,
          })}
          onClick={handleExpandClick}
          aria-expanded={expanded}
          aria-label="show more"
        >

        </IconButton>
      </CardActions>
      <Collapse in={expanded} timeout="auto" unmountOnExit>
        <CardContent>
          <Typography paragraph>Method:</Typography>
          <Typography paragraph>
            Heat 1/2 cup of the broth in a pot until simmering, add saffron and set aside for 10
            minutes.
          </Typography>
          <Typography paragraph>
            Heat oil in a (14- to 16-inch) paella pan or a large, deep skillet over medium-high
            heat. Add chicken, shrimp and chorizo, and cook, stirring occasionally until lightly
            browned, 6 to 8 minutes. Transfer shrimp to a large plate and set aside, leaving chicken
            and chorizo in the pan. Add pimentón, bay leaves, garlic, tomatoes, onion, salt and
            pepper, and cook, stirring often until thickened and fragrant, about 10 minutes. Add
            saffron broth and remaining 4 1/2 cups chicken broth; bring to a boil.
          </Typography>
          <Typography paragraph>
            Add rice and stir very gently to distribute. Top with artichokes and peppers, and cook
            without stirring, until most of the liquid is absorbed, 15 to 18 minutes. Reduce heat to
            medium-low, add reserved shrimp and mussels, tucking them down into the rice, and cook
            again without stirring, until mussels have opened and rice is just tender, 5 to 7
            minutes more. (Discard any mussels that don’t open.)
          </Typography>
          <Typography>
            Set aside off of the heat to let rest for 10 minutes, and then serve.
          </Typography>
        </CardContent>
      </Collapse>
    </Card>
  );
}